@use '../../Foundation/vars' as va;

.scroll-bar{
    width:1px;
    height:100px;
    background-color: va.$text-color;
    //下寄せに
    position: relative;
    left:50%;
    &::after{
        content: "SCROLL";
        position:absolute;
        left:10.5px;
        writing-mode: vertical-lr;
        font-size: max(calc(10/va.$pc-design-width*100vw),10px);
        font-family: va.$english;
        color: va.$text-color
    }
}