@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.section_instagram_text{
    font-family: va.$english;
    color: #fff;
    font-weight: 500;
    font-size: max(calc(20/va.$sp-design-width*100vw),20px);
    letter-spacing: calc(80/1000*1em);
    margin-bottom:calc(40/va.$sp-design-width*100vw);
    //margin-bottom: max(calc(40/va.$sp-design-width*100vw),40px);
    //height: max(calc(80/va.$sp-design-width*100vw),80px);
    @include mixin.mq(pc){
        font-size:max(calc(28/va.$pc-design-width*100vw),28px);
        //margin-bottom: max(calc(20/va.$pc-design-width*100vw),20px);
        line-height: calc(32/28);
        margin-bottom:calc(40/va.$pc-design-width*100vw);
    }
}