@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_shop-info_image--yakiimo{
    background-image: url(../img/shop-info-yakiimo.jpg);
    width: calc(200/va.$sp-design-width*100vw);
    height:calc((200/va.$sp-design-width*100vw));
    background-size: cover;
    background-repeat: no-repeat;
    display: none;
    @include mixin.mq(pc){
        display: block;
        width: calc(480/va.$pc-design-width*100vw);
        //width: 100%;
        height:calc((300/va.$pc-design-width*100vw));
        margin: 0 0 0 auto;
        margin-top: max(calc(-240/va.$pc-design-width*100vw),-240px);
        //margin-top:-180px;
        margin-bottom: calc(100/va.$pc-design-width*100vw);
    }
}