@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_shop-info_heading2{
    //width: 100%;
    color: #fff;
    font-family: va.$serif;
    font-weight: 500;
    font-size: max(calc(20/va.$sp-design-width*100vw),20px);
    line-height: calc(40/28);
    //ext-align: center;
    writing-mode: vertical-lr;
    margin-bottom: max(calc(40/va.$sp-design-width*100vw),40px);
    position: relative;
    margin:0 auto;
    padding-top: max(calc(40/va.$sp-design-width*100vw),40px);
    letter-spacing: calc(450/1000*1em);
    //中央に寄せるための記述
    //left: max(calc(145/va.$sp-design-width*100vw),145px);
    &::after{
        content: "shop info";
        position:relative;
        left: max(calc(30.5/va.$sp-design-width*100vw),30.5px);
        bottom: max(calc(115/va.$sp-design-width*100vw),115px);
        //writing-mode: vertical-lr;
        font-size: max(calc(12/va.$pc-design-width*100vw),12px);
        font-family: va.$english;
        color: #fff;
        text-transform: uppercase;
        letter-spacing: calc(190/1000*1em);
        line-height: calc(16/12);
        @include mixin.mq(pc){
            bottom: max(calc(163/va.$pc-design-width*100vw),163px);
            left: max(calc(40/va.$pc-design-width*100vw),40px);
            font-size: max(calc(12/va.$pc-design-width*100vw),12px);
        }
    }
    @include mixin.mq(pc){
        font-size:max(calc(28/va.$pc-design-width*100vw),28px);
        //margin-bottom: max(calc(85/va.$pc-design-width*100vw),85px);
        padding-top: max(calc(200/va.$pc-design-width*100vw),200px);
        //中央に寄せるための記述
        //position: relative;
        //left: calc(175/va.$pc-design-width*100vw);
    }
}