@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_garelly_treatment{
    background-image: url(../img/garelly-treatment.png);
    width: calc(200/va.$sp-design-width*100vw);
    height:calc((200/va.$sp-design-width*100vw));
    background-size: cover;
    background-repeat: no-repeat;
    @include mixin.mq(pc){
        width: calc(505/va.$pc-design-width*100vw);
        height:calc((466/va.$pc-design-width*100vw));
    }
}