@use '../../Foundation/mixin';
@use '../../Foundation/vars' as va;

.article_menu_main-image{
    width: 100%;
    object-fit: contain;
    margin-bottom: calc(40/va.$sp-design-width*100vw);
    @include mixin.mq(pc){
        width:calc(1286/va.$pc-design-width*100vw);
        margin-bottom: calc(60/va.$pc-design-width*100vw)
    }
}