@use '../../Foundation/mixin';
@use '../../Foundation/vars' as va;

.header_nav-body{
    display: none;
    @include mixin.mq(pc){
        display:block;
        margin-right:max(calc(110/va.$pc-design-width*100vw),110px);
        margin-bottom: max(calc(56/va.$pc-design-width*100vw),56px);
    }
}