@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.sidebar_wrapper{
    width: 100%;
    position:fixed;
    background-color: va.$main-color;
    top: 0;
    right:0;
    //right: 73.8%;
    z-index: 10;
    opacity: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    //justify-content: center;
    /* right0から250px移動した位置 サイドバーの幅で画面外に移動させている
    transform: translateX(max(calc(410/va.$pc-design-width*100vw),410px)); 
    */
    /* 浮かび上がってくる速さ*/
    transition: all .4s;
    @include mixin.mq(pc){
        width:max(calc(410/va.$pc-design-width*100vw),410px);   
    } 
}

.sidebar_wrapper.is-show{
    opacity: 1;
    /*
    transform: translateX(0);  right0まで-250px移動 
    width:max(calc(410/va.$pc-design-width*100vw),410px);
    */
    height: 100%;
    margin:0 auto;
    z-index: 9999;
    justify-content: center;
}