@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu_link{
    @include mixin.button-sub-color();
    margin-bottom: calc(120/va.$sp-design-width*100vw);
    @include mixin.mq(pc){
        width:calc(380/va.$pc-design-width*100vw);
        margin-bottom: min(calc(190/va.$pc-design-width*100vw),190px);
        padding: calc(25/va.$pc-design-width*100vw) 0;
        font-size: max(calc(16/va.$pc-design-width*100vw),16px);
    }    
}

