@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.section_instagram_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: calc(80/va.$pc-design-width*100vw) 5%;
    width: 100%;
    //flex-direction: column;
    @include mixin.mq(pc){
        flex-direction: row;
        padding: calc(160/va.$pc-design-width*100vw) calc(140/va.$pc-design-width*100vw);
        position:relative;
    }
}