@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.section_instagram_icon{
    background-image: url(../img/instagram-icon.png);
    width: calc(50/va.$sp-design-width*100vw);
    height:calc(50/va.$sp-design-width*100vw);
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0 auto;
    //display: none;
    @include mixin.mq(pc){
        //display: block;
        width: calc(50/va.$pc-design-width*100vw);
        //width: 100%;
        height:calc(50/va.$pc-design-width*100vw);      
    }
}