@use '../../Foundation/mixin';
@use '../../Foundation/vars' as va;

.header_logo{
    width: calc(125/va.$sp-design-width*100vw);
    height: calc(125/va.$sp-design-width*100vw);
    margin-top: calc(25/va.$sp-design-width*100vw);
    margin-left: calc(30/va.$sp-design-width*100vw);
    @include mixin.mq(pc){
        width: calc(225/va.$pc-design-width*100vw);
        height: calc(225/va.$pc-design-width*100vw);
        margin-top: calc(35/va.$pc-design-width*100vw);
        margin-left: calc(40/va.$pc-design-width*100vw);
    }
}