@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_shop-info_text{
    font-family: va.$sans;
    color: #fff;
    font-weight: 500;
    font-size: max(calc(16/va.$sp-design-width*100vw),16px);
    //line-height: calc(50/16);
    letter-spacing: calc(80/1000*1em);
    margin-bottom: max(calc(40/va.$sp-design-width*100vw),40px);
    display: inline-block;
    //height: max(calc(80/va.$sp-design-width*100vw),80px);
    @include mixin.mq(pc){
        font-size:max(calc(16/va.$pc-design-width*100vw),16px);
        margin-bottom: max(calc(35/va.$pc-design-width*100vw),35px);
        line-height: calc(20/16);
    }
}