@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu_heading3{
    font-family: va.$serif;
    color: #fff;
    font-weight: 500;
    font-size: max(calc(20/va.$sp-design-width*100vw),20px);
    line-height: calc(27/18);
    letter-spacing: calc(100/1000*1em);
    margin-bottom: max(calc(20/va.$sp-design-width*100vw),20px);
    @include mixin.mq(pc){
        font-size:max(calc(28/va.$pc-design-width*100vw),28px);
        margin-bottom: max(calc(25/va.$pc-design-width*100vw),25px);
        line-height: calc(40/28);
        max-width: 800px;
    }
}