@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_commit_text{
    font-family: va.$sans;
    color: va.$text-color;
    letter-spacing: calc(80/1000*1em);
    line-height: calc(32/16);
    font-size: max(calc(16/va.$sp-design-width*100vw),16px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 max(calc(20/va.$sp-design-width*100vw),20px);
    @include mixin.mq(pc){
        font-size: max(calc(20/va.$pc-design-width*100vw),20px);
    }
}