@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu_sub-image_wrapper{
    width: 100%;
    margin-bottom: max(calc(50/va.$sp-design-width*100vw),50px);
    @include mixin.mq(pc){
        display: flex;
        width:max(calc(1120/va.$pc-design-width*100vw));
        justify-content: space-between;
        margin-bottom: max(calc(100/va.$pc-design-width*100vw),100px);
    }
}