@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu{
    background-color: va.$main-color;
    padding: calc(80/va.$sp-design-width*100vw) 5% calc(120/va.$pc-design-width*100vw);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @include mixin.mq(pc){
        padding:calc(150/va.$pc-design-width*100vw) 0 calc(190/va.$pc-design-width*100vw);
    }
}