@use '../../Foundation/mixin';
@use '../../Foundation/vars' as va;

.article_shop-info_bottom{
    background-color: va.$main-color;
    height: max(calc(80/va.$sp-design-width*100vw),80px);
    @include mixin.mq(pc){
        height: calc(245/va.$pc-design-width*100vw);
        width:calc(380/va.$pc-design-width*100vw);
        //margin-bottom: calc(245/va.$pc-design-width*100vw);
        padding: calc(25/va.$pc-design-width*100vw) 0;
        font-size: max(calc(16/va.$pc-design-width*100vw),16px);
    }
}