@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_shop-info_treatment{
    background-image: url(../img/shop-info-treatment.jpg);
    width: calc(200/va.$sp-design-width*100vw);
    height:calc((200/va.$sp-design-width*100vw));
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 0 0 auto;
    @include mixin.mq(pc){
        width: calc(465/va.$pc-design-width*100vw);
        //width: 100%;
        height:calc((480/va.$pc-design-width*100vw));
    }
}