@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu_text{
    font-family: va.$sans;
    color: #fff;
    font-weight: 500;
    font-size: max(calc(16/va.$sp-design-width*100vw),16px);
    line-height: calc(27/18);
    letter-spacing: calc(80/1000*1em);
    margin-bottom: max(calc(60/va.$sp-design-width*100vw),60px);
    @include mixin.mq(pc){
        font-size:max(calc(16/va.$pc-design-width*100vw),16px);
        margin-bottom: max(calc(90/va.$pc-design-width*100vw),90px);
        line-height: calc(32/16);
        max-width: 800px;
    }
}