@use '../../Foundation/vars' as va;

.sidebar_sns_text{
    position: relative;
    //スマホの時は消えているのでPCサイズのみ
    font-size:max(calc(13/va.$pc-design-width*100vw),13px);
    color: va.$sub-color;
    //writing-mode: vertical-lr;
    font-family: va.$english;
    letter-spacing: calc(40/1000*1em);
    line-height: calc(17/13);
    margin-right:max(calc(40/va.$pc-design-width*100vw),40px);
    text-transform: uppercase;
    &::after{
        position: absolute;
        content: "";
        width:max(calc(23/va.$pc-design-width*100vw),23px);
        height:1px;
        top:19px;
        left:0px;
        background-color: va.$sub-color;
        //transform: rotate(90deg);
    }
}