@use '../../Foundation/vars' as va;

.top_sns_text{
    position: relative;
    //スマホの時は消えているのでPCサイズのみ
    font-size:max(calc(13/va.$pc-design-width*100vw),13px);
    color: #2B0603;
    writing-mode: vertical-lr;
    font-family: va.$english;
    letter-spacing: calc(40/1000*1em);
    line-height: calc(17/13);
    margin-bottom:max(calc(25/va.$pc-design-width*100vw),25px);
    text-transform: uppercase;
    &::after{
        position: absolute;
        content: "";
        width:max(calc(23/va.$pc-design-width*100vw),23px);
        height:1px;
        top:12px;
        right:10px;
        background-color: #2B0603;
        transform: rotate(90deg);
    }
}