@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_menu_heading2{
    color:#fff;
    font-family: va.$serif;
    font-weight: 500;
    font-size: max(calc(20/va.$sp-design-width*100vw),20px);
    line-height: calc(40/28);
    //ext-align: center;
    writing-mode: vertical-lr;
    margin-bottom: max(calc(40/va.$pc-design-width*100vw),40px);
    position: relative;
    margin:0 auto;
    letter-spacing: calc(450/1000*1em);
    &::after{
        content: "menu";
        position:relative;
        left: max(calc(30.5/va.$sp-design-width*100vw),30.5px);
        bottom: max(calc(115/va.$sp-design-width*100vw),115px);
        //writing-mode: vertical-lr;
        font-size: max(calc(12/va.$sp-design-width*100vw),12px);
        font-family: va.$english;
        color: #fff;
        text-transform: uppercase;
        line-height: calc(16/12);
        letter-spacing: calc(200/1000*1em);
        @include mixin.mq(pc){
            bottom: max(calc(160/va.$pc-design-width*100vw),16px);
            left: max(calc(40/va.$pc-design-width*100vw),40px);
            font-size: max(calc(12/va.$pc-design-width*100vw),12px);
        }
    }
    @include mixin.mq(pc){
        font-size:max(calc(28/va.$pc-design-width*100vw),28px);
        margin-bottom: max(calc(80/va.$pc-design-width*100vw),80px);
    }
}