@use '../../Foundation/mixin';
@use '../../Foundation/vars' as va;

.nav-btn {
        position: fixed;
        right: 0;
        z-index: 10000;
        display:block;
        margin-top: max(calc(40/va.$sp-design-width*100vw),40px);
        margin-right: max(calc(40/va.$sp-design-width*100vw),40px);
        margin-bottom: max(calc(250/va.$sp-design-width*100vw),250px);
    @include mixin.mq(pc){
        margin-top: max(calc(40/va.$pc-design-width*100vw),40px);
        margin-right: max(calc(40/va.$pc-design-width*100vw),40px);
        margin-bottom: max(calc(250/va.$pc-design-width*100vw),250px);
    }
    > a {
        display: block;
        position: relative;
        width: 61px;
        height: 36px;
        z-index: 10;
    }
    > a::before {
        position:absolute;
        top: 15px;
        //left: 14px;
        content: "";
        width: 61px;
        height: 1px;
        background-color:va.$text-color;
        transition: .2s;
    }
    > a > span {
        display: block;
        overflow: hidden;
        width: 1px;
        height: 1px;
    }
    > a > span::after{
        position:absolute;
        top: 25px;
        //left: 14px;
        content: "";
        width: 61px;
        height: 1px;
        background-color:va.$text-color;
        transition: .2s;
    }
    > a::after {
        position:absolute;
        top: 35px;
        left: 1px;
        content: "menu";
        font-family: va.$english;
        font-size:12px;
        letter-spacing: calc(600/1000*1em);
        line-height: calc(16/12);
        width: 61px;
        height: 2px;
        //background-color:#000;
        color: va.$text-color;
        text-transform: uppercase;
        white-space: nowrap;
        font-weight: 500;
    }
    //ハンバーガーメニューのボタンを押したときの挙動
    > a.is-open::before {
        top: 24px;
        transform: rotate(-15deg);
        background-color: va.$sub-color;
    }
    > a.is-open::after {
        //display:none;
        color: va.$sub-color;
    }
    > a.is-open > span::after {
        bottom: 24px;
        transform: rotate(15deg);
        background-color: va.$sub-color;
    }
}