@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.sidebar_items{
    //PC
    display: flex;
    flex-direction: column;
    @include mixin.mq(pc){
        flex-direction: row-reverse;
        justify-content: space-between;
        width:calc(222/va.$pc-design-width*100vw);
        //margin-left:30%;
        //margin-top:30%;
        margin-top:max(calc(252/va.$pc-design-width*100vw),252px);    
    }
}